<template>
  <footer class="app-footer flex space-between align-center" v-bind:style="{ backgroundColor: user.color }">
    <p>Mister book</p>
    <div>
      <chat-box v-if="isChatShown">
        <div slot="header" class="chat-header flex space-between align-center">
          <h3>Chat Support</h3>
          <button @click="setChatShown" class="esc-btn">Esc</button>
        </div>
      </chat-box>

      <edit-user v-if="isEditUserShown" v-model="user.color">
        <div slot="header" class="edit-user-header flex space-between align-center">
          <h3>Edit User</h3>
          <button @click="setEditUserShown" class="esc-btn">Esc</button>
        </div>
      </edit-user>

      <div class="flex">
        <button
          v-if="!isEditUserShown"
          @click="setEditUserShown"
          class="edit-user-btn"
        >
          Edit User Profile
        </button>
        <button v-if="!isChatShown" @click="setChatShown" class="open-chat-btn">
          Start a chat
        </button>
      </div>
    </div>
  </footer>
</template>



<script>
import ChatBox from "@/cmps/ChatBox";
import EditUser from "@/cmps/EditUser";

export default {
  data() {
    return {
      user: {
        color:'#b83b5e'
      },
      isChatShown: false,
      isEditUserShown: false,
    };
  },
  components: {
    ChatBox,
    EditUser,
  },
  methods: {
    setChatShown() {
      this.isChatShown = !this.isChatShown;
    },
    setEditUserShown() {
      this.isEditUserShown = !this.isEditUserShown;
    },
  },
};
</script>
