<template>
  <div class="user-edit">
    <slot name="header"></slot>
    <!-- <v-color-picker dot-size="25" swatches-max-height="200"></v-color-picker> -->
    <div class="flex color-wrapper">
      <p>Choose a color:</p>
      <v-swatches v-model="color"></v-swatches>
    </div>
      <button @click="changeColor">Done</button>
  </div>
</template>

<script>
import VSwatches from "vue-swatches";
import "vue-swatches/dist/vue-swatches.css";

export default {
  components: {
    VSwatches,
  },
  data() {
    return {
      color: "#B83B5E",
    };
  },
  methods: {
    changeColor() {
      this.$emit("input", this.color);
    },
  },
};
</script>
